@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.13.0/css/all.min.css');

*{
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
    margin: 0;
}



h1{
    font-weight: 500;
}

.listContainer {
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    height: 100vh;
    overflow: auto;
    background-color: #f5f5f5;
}

ul {
    list-style: none;
}

li {
    width: 400px;
    display: flex;
    padding-top: 15px;
    justify-content: space-between;
}

.icons {
    width: 100px;
    display: flex;
    justify-content: space-between;
}

.far {
    font-family: 'Font Awesome 5 Free';
    font-weight: 400;
}
.line {
    
    color: rgba(149, 165, 166, 0.7);
    text-decoration: line-through;
    transition: 0.3s ease;
}

.listContainer input{

    width: 500px;
    height: 40px;
    margin: 0;
    background-color: #f5f5f5;
    border: 0.5px solid #80cfa9;
    border-collapse: collapse;
    transition: 0.5s ease-in-out;
    padding-left: 5px;
}

.listContainer input:focus{
    width: 500px;
    height: 40px;
    margin: 0;
    background-color: #fff;
    padding: 0;
    padding-left: 5px;
    font-size: 18px;
    line-height: 18px;
    
}

::placeholder {
    font-size: 18px;
    padding-left: 5px;
}

.listContainer button {
    height: 40px;
    width: 70px;
    font-size: 18px;
    background-color: #f5f5f5;
    border: 0.5px solid #80cfa9;
    border-collapse: collapse;
    transition: 0.5s ease;
}

.listContainer button:hover {
    height: 40px;
    width: 70px;
    font-size: 18px;
    background-color: #80cfa9;
    border: 0.5px solid #80cfa9;
    border-collapse: collapse;
}

form {
    padding-top: 15px;
    display: flex;
}

#root {
    display: flex;
    justify-content: center;
    align-items: center;
}

.listItem {
    display: flex;
    width: 100%;
    justify-content: space-between;
    border: 1px solid black;
}

.listItem p {
    padding-right: 20px;
    padding-left: 20px;
}

button.clearAll {
    margin-top: 30px;
    width: 200px;
}

button.clearAll:hover {
    width: 200px;
    font-size: 18px;
    background-color: #80cfa9;
    border: 0.5px solid #80cfa9;
    border-collapse: collapse;
}